export default function (params) {
    var type = params.type || 'warn';
    var component = params.source && "Source \u2014 <" + params.source.constructor.name + " />";
    var message = "From Themeor (http://themeor.opium.pro)\n\n" + (component || '') + "\n\n" + params.text + "\n";
    console[type](message);
}
